___CSS_LOADER_EXPORT___.push([module.id, `body {
  padding: 0;
  margin: 0;
  font-family: 'Roboto', sans-serif;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  background-color: #333333;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.main-content,
.options {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main-content {
  flex-direction: column;
}
.app {
  width: 70%;
}
.app-container {
  padding: 40px 10px;
  color: white;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(30px);
  border: 1px solid rgba(255, 255, 255, 0.089);
  height: fit-content;
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
}

.place {
  display: grid;
  grid-template-rows: 2fr 1fr;
  justify-content: center;
  align-items: center;
  height: 10em;
  overflow: visible;
}
.place-other {
  display: flex;
  flex-direction: column;
}

.place-name {
  font-size: 3.5em;
  font-weight: 500;
  margin-top: auto;
}

.region-name {
  font-size: 1.5em;
}

/*Now information*/
.now-info {
  display: grid;
  grid-template-rows: 2fr 1fr;
  min-height: 8em;
  max-height: 11em;
  gap: 15px;
  justify-content: center;
  align-items: center;
}

.now-weather {
  display: flex;
  gap: 1em;
}

.now-condition {
  font-size: 1em;
  font-weight: 500;
  font-style: italic;
}

.weather-icon {
  height: 65px;
  width: 65px;
}

.icon-condition {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  max-width: 120px;
}

.temp-api {
  font-size: 3em;
  align-self: center;
}

.feels-like,
.humidity,
.wind {
  display: flex;
  align-items: center;
  gap: 5px;
}

.icons {
  height: 1.5em;
}
.loading {
  filter: blur(6px);
  z-index: 1;
  pointer-events: none;
}
.loader {
  position: absolute;
  display: none;
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  box-sizing: border-box;
  border-top: 8px solid #fff;
  border-left: 8px solid #fff;
  border-right: 8px solid #ff00;
  animation: loader 0.7s infinite linear;
  z-index: 2;
}

@keyframes loader {
  to {
    transform: rotate(360deg);
  }
}
@media (min-width: 1400px) {
  .app {
    width: 55%;
  }
}
