___CSS_LOADER_EXPORT___.push([module.id, `.options {
  width: 70%;
  height: 5em;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: auto;
}

.location {
  display: flex;
  order: 1;
  margin: 0 auto;
}

.toggle-button-cover {
  order: 2;
}
.search-group {
  display: flex;
  line-height: 25px;
  align-items: center;
  position: relative;
  max-width: 250px;
}

.search-place {
  width: 100%;
  height: 35px;
  line-height: 28px;
  padding: 0 1rem;
  padding-left: 2.5rem;
  border: 2px solid transparent;
  border-radius: 15px;
  outline: none;
  background-color: #f3f3f4;
  color: #0d0c22;
  transition: 0.3s ease;
}

.search-place::placeholder {
  color: #9e9ea7;
}

.search-place:focus,
.search-place:hover,
#getLocationBtn:hover {
  outline: none;
  border-color: rgba(234, 226, 183, 0.4);
  background-color: #fff;
  box-shadow: 0 0 0 4px rgb(234 226 183 / 10%);
}

.search-place-btn {
  position: absolute;
  left: 1rem;
  fill: #9e9ea7;
  width: 1rem;
  height: 1rem;
  cursor: pointer;
}

#getLocationBtn {
  margin-left: 0.5em;
  display: flex;
  background-color: #f3f3f4;
  border-radius: 50%;
  padding: 5px;
  cursor: pointer;
  align-items: center;
  justify-content: center;
  transition: 0.3s ease;
}

.get-location {
  height: 1.5em;
}
@media (min-width: 1400px) {
  .options {
    width: 55%;
  }
}
